<template>
  <Modal
    :active="showModal"
    :event-name="'remove-confirmation-modal'"
    :custom-padding="'p-spacing-0'"
    :prevent-close="true"
  >
    <div class="flex h-full min-h-[300px] w-full flex-col">
      <GeneralForm
        :heading="undefined"
        :sub-heading="undefined"
        :showCloseButton="true"
        @close-pressed="handleClose"
        @form-submitted="handleConfirm"
        :is-form-submitting="isFormSubmitting"
        :submit-button-label="'Delete'"
        :submit-button-type="'destructive'"
        :form-type="'delete'"
        class="flex-1"
      >
        <template #form-body>
          <div class="flex flex-col gap-spacing-300">
            <p
              class="text-headline/m/relaxed/500 text-content-on-neutral-primary"
            >
              Are you Sure?
            </p>
            <p
              class="text-body/m/relaxed/400 text-content-on-neutral-secondary"
            >
              {{ confirmationText }}
            </p>
            <div
              v-if="checkboxDescription"
              class="flex cursor-pointer items-center gap-spacing-100 text-body/m/relaxed/400 text-content-on-neutral-primary"
              @click.stop="optionSelected = !optionSelected"
            >
              <GeneralCheckbox :selected="optionSelected" />
              <p>
                {{ checkboxDescription }}
              </p>
            </div>
          </div>
        </template>
      </GeneralForm>
    </div>
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    GeneralForm: () => import("@/components/General/Form.vue"),
    GeneralCheckbox: () => import("@/components/General/Checkbox.vue"),
  },
  data() {
    return {
      showModal: false,
      confirmationText: "This item will be deleted permanently.",
      isFormSubmitting: false,
      optionSelected: true,
      emitterHandler: null,
      checkboxDescription: "",
      confirmEvent: "",
      id: null,
    };
  },
  mounted() {
    this.emitterHandler = (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        if (data?.confirmationText) {
          this.confirmationText = data?.confirmationText;
        }
        if (data?.checkboxDescription) {
          this.checkboxDescription = data?.checkboxDescription;
        }
        if (data?.confirmEvent) {
          this.confirmEvent = data?.confirmEvent;
        }
        if (data?.id) {
          this.id = data?.id;
        }
      } else {
        this.showModal = false;
        this.buttons = null;
        this.confirmationText = "This item will be deleted permanently.";
        this.isFormSubmitting = false;
        this.checkboxDescription = "";
        this.confirmEvent = "";
        this.id = null;
      }
    };
    this.$bus.$on("remove-confirmation-modal", this.emitterHandler);
  },
  methods: {
    handleFieldChange(type) {
      this.selectedDataMissingOption = type;
    },
    handleClose() {
      this.$bus.$emit("remove-confirmation-modal", false);
    },
    handleConfirm() {
      if (this.confirmEvent) {
        this.$bus.$emit(this.confirmEvent, {
          optionSelected: this.optionSelected,
          id: this?.id,
        });
      }
      this.handleClose();
    },
    handleBack() {
      this.handleClose();
    },
  },
  beforeDestroy() {
    this.$bus.$off("remove-confirmation-modal", this.emitterHandler);
  },
};
</script>
